.mobile img {
  width: 150px;
  padding: 10px;
}

.mobile .icon {
  width: 30px;
  height: 30px;
  float: right;
}

.nav-hide {
  display: none;
}

.mobile {
  display: block;
}

@media (min-width: 1024px) {
  .mobile {
    display: none;
  }
}

.mobile ul {
  padding: 0px;
  text-align: center;
  animation: puffInBig 600ms both;
}

.mobile ul li {
  background-color: #03a9f4;
  padding: 10px 0;
}

.mobile ul li a.nav-link {
  text-decoration: none;
  color: black;
  font-weight: bold;
  font-size: 15px;
}

.mobile ul li:nth-child(even) {
  background-color: #fa9d57;
}

.large {
  background: white;
  opacity: .8;
  position: fixed;
  width: 100%;
  z-index: 1;
}

.large ul img {
  width: 70%;
}

.large ul li {
  list-style: none;
}

.large ul li::hover {
  box-shadow: -3px 2px lightblue;
  background-color: #03a9f4;
  padding: 10px;
}

.large ul a {
  color: black;
  text-decoration: none;
  font-size: 15px;
  font-weight: 700;
}

@media (min-width: 0px) {
  .large ul {
    display: none;
  }
}

@media (min-width: 1024px) {
  .large ul {
    display: grid;
    grid-template-columns: repeat(5, 1fr);
    padding: 0px;
    justify-items: center;
    align-items: center;
  }
}

@keyframes puffInBig {
  0% {
    transform: scale3d(10, 10, 10);
    opacity: 0;
  }
  100% {
    transform: none;
    opacity: 1;
  }
}

#fullpage section {
  overflow: hidden;
}

#fullpage section:nth-child(1) {
  background-image: url(../../../assets/web-design-2038872_1920.jpg);
  background-size: 100% auto;
  background-repeat: no-repeat;
}

@media (min-width: 1024px) {
  #fullpage section:nth-child(1) {
    height: 100vh;
    align-items: center;
  }
}

#fullpage section:nth-child(1) .content {
  width: 85%;
  margin: 20% auto;
  text-align: center;
  animation: zoomIn 600ms ease-in-out both;
  transform-style: preserve-3d;
}

#fullpage section:nth-child(1) .content h1 {
  color: #fb9d57;
}

#fullpage section:nth-child(1) .content h3 {
  background-color: #5c6163;
  display: inline;
  color: #e4dfcb;
  border-radius: 0.4em;
  padding: 5px;
}

#fullpage section:nth-child(1) .content h1 span {
  color: #03a9f4;
}

.about {
  padding: 120px 0;
}

.about h1 span {
  color: #03a9f4;
}

.about .img {
  width: 287px;
  border-radius: 50%;
  height: 284px;
}

.about .cont_pics {
  width: 90%;
  margin: 0px auto;
  text-align: center;
}

.about h1 {
  text-align: center;
  margin: 20px 0px;
  font-size: 250%;
  padding-bottom: 30px;
}

.about .about-para {
  width: 90%;
  margin: 0px auto;
}

.about .about-para p {
  text-align: justify;
  display: block;
}

.about .about-para ul {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  padding: 0px;
  justify-items: center;
  grid-gap: 10px;
}

.about .about-para ul li {
  margin: 0px auto;
  list-style: none;
}

@media (min-width: 1024px) {
  .about {
    width: 80%;
    margin: 0px auto;
  }
}

@media (min-width: 1024px) {
  .about .container {
    display: grid;
    grid-template-columns: 1fr 2fr;
    grid-gap: 20px;
  }
  .about .container .cont_pics {
    width: 100%;
  }
  .about .container ul {
    display: grid;
    grid-template-columns: repeat(7, auto);
  }
}

.portfolio {
  padding-bottom: 120px;
  width: 90%;
  margin: 0 auto;
}

.portfolio h1 {
  text-align: center;
  margin: 20px 0px;
  font-size: 250%;
  padding-top: 30px;
}

.portfolio h4 {
  text-align: center;
  margin: 15px;
}

.portfolio img {
  height: 254px;
  width: 100%;
}

@media (min-width: 768px) {
  .portfolio {
    width: 80%;
  }
  .portfolio .port-container {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-gap: 15px;
  }
}

.contact {
  padding-bottom: 120px;
}

.contact .container {
  width: 90%;
  text-align: center;
  margin: auto;
}

.contact .container iframe {
  height: 704px;
  width: 100%;
}

.contact .contact-info {
  box-shadow: 4px 5px 17px black;
  background-color: #e7e7e7;
  text-align: center;
}

.contact .contact-info:hover {
  transition: 1000ms;
  transform: scale(1.03);
}

.contact .contact-info ul {
  padding: 0px;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
}

.contact .contact-info ul li {
  margin: 2px auto;
  list-style: none;
  height: 33px;
}

.contact .contact-info ul li i {
  color: black;
}

@keyframes zoomIn {
  from {
    opacity: 0;
    transform: scale3d(0.3, 0.3, 0.3);
  }
}

footer {
  background-color: #000;
  opacity: .5;
  color: #e4dfcb;
  text-align: center;
  height: 30px;
  padding: -4px 0px;
  padding-top: 18px;
}

